import styles from './Error.module.css'

export const Error404 = () =>{
    const navigate = useNavigate()
    return(
        <div className={styles.error}>
            <h1> 404</h1>
            <p> -   ! ,     .
                  ,  ,        .
            </p>
            <div className={styles.link}>
                <button onClick={() => navigate('/')}>  </button>
            </div>
        </div>
    )
}