import styles from './MainPage.module.css'
import { Header } from "../../components/header/Header"
import { Footer } from "../../components/footer/Footer"
import { Link, useNavigate } from "react-router-dom"
import { useContext, useEffect } from "react"
import { Context } from '../..'
import { notify } from '../../utils/Consts'

export const MainPage = () => {
    const navigate = useNavigate()
    const {store} = useContext(Context)

    useEffect(() => {
        document.title = ' '
    })

    return(
        <div className={styles.page}>
            <Header />
            <div className={styles.container}>
                <Link to='/AccountCompany'></Link>
                <div className={styles.firstContainer}>
                    <div className={styles.fisrtContent}>
                        <div className={styles.firstText}>
                            <h1>  </h1>
                            <img src='/img/text.svg' alt='text'/>
                            <p>      .
                                      
                                    .   - 
                                .</p>
                        </div>
                        <div className={styles.firstSvg}>
                            <img src='/img/content.svg' alt='content'/>
                        </div>
                    </div>
                    <img className={styles.img} src='/img/star.svg' alt='star'/>
                </div>
                <div className={styles.svg}>
                </div>
                <div className={styles.secondContainer}>
                    <div className={styles.secondContent}>
                        <div className={styles.vacancy}>
                            <div className={styles.new}>
                                <h1></h1>
                                <hr></hr>
                            </div>
                            <div className={styles.featured}>
                                <h1></h1>
                                <hr></hr>
                            </div>
                            <div className={styles.popular}>
                                <h1></h1>
                                <hr></hr>
                            </div>
                            <div className={styles.recent}>
                                <h1></h1>
                                <hr></hr>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={styles.svg1}>
                </div>
                <div className={styles.thirdContainer}>
                    <div className={styles.thirdContent}>
                        <div className={styles.secondText}>
                            <h1> </h1>
                            <p> </p>
                        </div>
                        {store.IsAuth ?(
                            <div className={styles.textContent}>
                                <div className={styles.block}>
                                    <p>     
                                          ,   
                                           .  
                                           117  .   
                                        ,     .</p>
                                    <button
                                        onClick={() => navigate('/PostVacancy')}> </button>
                                </div>
                                <div className={styles.block1}>
                                    <p>      ,  
                                            .   
                                            .</p>
                                    <button 
                                        onClick={() => navigate('/CreateResume')}> </button>
                                </div>
                            </div>
                        ):(
                            <div className={styles.textContent}>
                                <div className={styles.block}>
                                    <p>     
                                          ,   
                                           .  
                                           117  .   
                                        ,     .</p>
                                    <button
                                        id={styles.postVacancy}
                                        onClick={notify}> </button>
                                </div>
                                <div className={styles.block1}>
                                    <p>      ,  
                                            .   
                                            .</p>
                                    <button 
                                        id={styles.createResume}
                                        onClick={notify}> </button>
                                </div>
                            </div>
                        )}
                    </div>
                    <div className={styles.svg2}></div>
                </div>
            </div>
            <Footer />
        </div>
    )
}