import styles from './CreateResume.module.css'

import { Header } from '../../components/header/Header'
import { Footer } from '../../components/footer/Footer'
import { useEffect, useState } from 'react'
import { toast } from 'react-toastify'
import { createResume } from '../../api/resume-api'

export const CreateResume = () =>{
    useEffect(() => {
        document.title = ' '
    })

    const [name, setName] = useState('')
    const [number, setNumber] = useState('')
    const [city, setCity] = useState('')
    const [aboutYou, setAboutYou] = useState('')
    const [vacancy, setVacancy] = useState('')
    const [workExamples, setWorkExamples] = useState('')
    const [educational, setEducational] = useState('')

    const handleSubmit = (e) => {
        e.preventDefault();
    
        //    
        if (!number || !name || !city || !aboutYou|| !vacancy
            || !workExamples || !educational) {
            toast.error(',   !', {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
            })
          return
        }
        setName('')
        setCity('')
        setAboutYou('')
        setVacancy('')
        setWorkExamples('')
        setEducational('')
        setNumber('')

        if(number || name || city || aboutYou|| vacancy
            || workExamples || educational) {
        toast.success('   !', {
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "colored",
            });
        return
        }
      }

    return(
        <div className={styles.page}>
            <Header />
            <form className={styles.container} onSubmit={handleSubmit}>
                <div className={styles.title}>
                    <hr></hr>
                    <h1> </h1>
                    <hr></hr>    
                </div>  
                <hr className={styles.hrData}></hr>
                <h2 className={styles.briefInfo}>   </h2>
                <div className={styles.boxContent}>
                    <div className={styles.inputData}>
                        <label><span>*</span></label>
                        <input
                            placeholder='  '
                            type='text'
                            value={name}
                            onChange={(e) => setName(e.target.value)}></input>
                    </div>
                    <div className={styles.inputData}>
                        <label> <span>*</span></label>
                        <input
                            placeholder="+7 (999) 999-99-99"
                            type='text'
                            value={number}
                            onChange={(e) => setNumber(e.target.value)}></input>
                    </div>
                    <div className={styles.inputData}>
                        <label><span>*</span></label>
                        <input
                            placeholder=""
                            type='text'
                            value={city}
                            onChange={(e) => setCity(e.target.value)}></input>
                    </div>
                    <div className={styles.inputData}>
                        <label> </label>
                        <input
                            placeholder="      "
                            type='text'
                            value={aboutYou}
                            onChange={(e) => setAboutYou(e.target.value)}></input>
                    </div>
                    <div className={styles.inputData}>
                        <label> </label>
                        <input
                            placeholder="       . . "
                            type='text'
                            value={educational}
                            onChange={(e) => setEducational(e.target.value)}></input>
                    </div> 
                </div>
                <hr className={styles.hrData}></hr>
                <h2 className={styles.briefInfo}>   </h2>
                <div className={styles.boxContent}>
                    <div className={styles.inputData}>
                        <label><span>*</span></label>
                        <input
                            placeholder="Frontend-"
                            type='text'
                            value={vacancy}
                            onChange={(e) => setVacancy(e.target.value)}></input>
                    </div>
                    <div className={styles.inputData}>
                        <label> <span>*</span></label>
                        <input
                            type='text'
                            placeholder='C...'
                            value={workExamples}
                            onChange={(e) => setWorkExamples(e.target.value)}></input>
                    </div>
                </div>
                <div className={styles.boxContent}>           
                    <div className={styles.buttonSubmit}>
                        <button
                            type='submit' onClick={
                                async () => {
                                    const response = await createResume (name, number, city, aboutYou, vacancy, workExamples, educational)
                                    }
                            }> </button>
                    </div>
                </div>
            </form>
            <Footer />
        </div>
    )
}